import React from 'react'
import { graphql } from 'gatsby'

import Box from '@material-ui/core/Box'

// Components
import SiteHeader from '../components/Headers/SiteHeader'
import GridList from '../components/Lists/GridList'
import Paper from '@material-ui/core/Paper'

/**
 * ArticlesTagsTemplate template
 * @param pageContext
 * @param data
 * @param location
 * @returns {*}
 * @constructor
 */
function ArticlesTagsTemplate({ pageContext, data, location }) {
  const { tag } = pageContext
  const { edges } = data.allMarkdownRemark
  //const { edges, totalCount } = data.allMarkdownRemark
  //const description = `${totalCount} post${totalCount === 1 ? '' : 's'} tagged with "${tag}"`

  return (
    <SiteHeader title={tag} description={`Articles about ${tag}`} location={location}>
      <Box clone pt={16} pr={2} pb={2} pl={2}>
        <Paper elevation={0}>
          <GridList edges={edges} location={location} />
        </Paper>
      </Box>
    </SiteHeader>
  )
}

export const query = graphql`
  query tagQuery($tag: String) {
    allMarkdownRemark(
      filter: {
        fileAbsolutePath: { regex: "/(articles)/.*\\\\.md$/" }
        frontmatter: { tags: { in: [$tag] }, published: { ne: "No" } }
      }
      sort: { fields: [frontmatter___date], order: DESC }
      limit: 400
    ) {
      totalCount
      edges {
        node {
          excerpt(pruneLength: 180)
          timeToRead
          fields {
            slug
          }
          frontmatter {
            title
            subtitle
            date(formatString: "YYYY-MM-DD")
            publishedBy
            image {
              publicURL
            }
          }
        }
      }
    }
  }
`

export default ArticlesTagsTemplate
